import React, { useState } from 'react';
import './SurveyPage.css';
import { redirect } from 'react-router-dom';
import axios from 'axios';
import { Button } from 'react-bootstrap';
import '../../mainpage/font.css'
import { useEffect } from 'react';


var categories = ["A01", "A02", "A03", "C01"];
function SurveyPage() {
  const questions = [
    { id: 'question1', text: '1.      ?' },
    { id: 'question2', text: '2.    ?' },
    { id: 'question3', text: '3.      ?' },
    { id: 'question4', text: '4.        ?' },
    { id: 'question5', text: '5.        ?.' },
    { id: 'question6', text: '6.      ?' },
    { id: 'question7', text: '7.     ?' },
    { id: 'question8', text: '8.       ?' },
    { id: 'question9', text: '9.        ?' },
    { id: 'question10', text: '10.  ?' },
    { id: 'question11', text: '11.       ?' },
    { id: 'question12', text: '12. , ,     ?' },
    { id: 'question13', text: '13.       ?' },
    { id: 'question14', text: '14.  ?' },
    { id: 'question15', text: '15.         ?' }
  ];

  const choices = ['!', ' .', '!'];
  const questionsPerPage = 1;

  const [formData, setFormData] = useState({});
  const [currentPage, setCurrentPage] = useState(1);
  const [unansweredQuestions, setUnansweredQuestions] = useState([]);
  const scrollRef = React.createRef();
  const handleChange = (question, value) => {
    setFormData(prevData => ({
      ...prevData,
      [question]: value
    }));
  };

  const handleSubmit = () => {
    //if   
  };

  React.useEffect(() => {
    const defaultFormData = {};
    questions.forEach(question => {
      defaultFormData[question.id] = ' .';
    });
    setFormData(defaultFormData);
  }, []);

  useEffect(()=>{
    if(formData.question1 === '!'){
      localStorage.setItem("startTime1", '8');
    }
    if(formData.question1 === ' .'){
      localStorage.setItem("startTime1", '9');
    }
    if(formData.question1 === '!'){
      localStorage.setItem("startTime1", '10');
    }
    if(formData.question2 === '!'){
      localStorage.setItem("startTime2", '8');
    }
    if(formData.question2 === ' .'){
      localStorage.setItem("startTime2", '9');
    }
    if(formData.question2 === '!'){
      localStorage.setItem("startTime2", '10');
    }
    if(formData.question3 === '!'){
      categories = categories.filter(item => item !== "A02");
      var a = categories.length;
      if(a==2){
        a = 0;
      }
      if(a==3){
        a = 1;
      }
      localStorage.setItem("categories", a);
    }
    if(formData.question4 === '!'){
      categories = categories.filter(item => item !== "A01");
      var a = categories.length;
      if(a==2){
        a = 0;
      }
      if(a==3){
        a = 2;
      }
      console.log(categories);
      localStorage.setItem("categories", a);
    }
    
  }, [formData])
  const handleNextPage = () => {
    setCurrentPage(prevPage => prevPage + 1);
    scrollRef.current.scrollTop = 0;
  };

  const handlePrevPage = () => {
    setCurrentPage(prevPage => prevPage - 1);
    scrollRef.current.scrollTop = 0;
  };

  const handleChoiceClick = (questionId, choice) => {
    setFormData(prevData => ({
      ...prevData,
      [questionId]: choice
    }));
    setUnansweredQuestions(prevQuestions => prevQuestions.filter(question => question !== questionId));
  };

  const indexOfLastQuestion = currentPage * questionsPerPage;
  const indexOfFirstQuestion = indexOfLastQuestion - questionsPerPage;
  const currentQuestions = questions.slice(indexOfFirstQuestion, indexOfLastQuestion);

  React.useEffect(() => {
    const unanswered = currentQuestions.filter(question => !formData[question.id]);
    setUnansweredQuestions(unanswered.map(question => question.id));
    scrollRef.current.scrollTop = 0;
  }, [currentPage]);

  return (
    <div>
    <div style={{ textAlign: 'center', marginBottom: "5vh" }}>
      <h1 className="fd"></h1>
      </div>
      <form onSubmit={handleSubmit}>
        <div ref={scrollRef} className="survey-container">
          {currentQuestions.map(question => (
            <div key={question.id} className="dd">
              <label htmlFor={question.id} style={{ fontSize: '23px' }}>{question.text}</label> {/*   */}
              <br />
              <div className="button-group" style={{marginTop:"10vh"}}>
                {choices.map(choice => (
                  <button
                    key={choice}
                    type="button"
                    className={`option-button ${formData[question.id] === choice ? 'active' : ''}`}
                    onClick={() => handleChange(question.id, choice)}
                  >
                    {choice}
                  </button>
                ))}
              </div>
              <br />
            </div>
          ))}
        </div>
        <div className="button-group">
          {currentPage > 1 && (
            <Button className="sd" variant="success" onClick={handlePrevPage}> </Button>
          )}
          {currentPage < Math.ceil(questions.length / questionsPerPage) && (
            <Button className="sd" onClick={handleNextPage}> </Button>
          )}
        </div>
        {currentPage === Math.ceil(questions.length / questionsPerPage) && (
          <button type="submit"></button>
        )}
      </form>
    </div>
  );
}

export default SurveyPage;
